@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
*{box-sizing:border-box}body,button,input,select{font-family:'Poppins', sans-serif}body{padding:0;margin:0;font-family:'Poppins', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#FAFAFA;background-color:#212121}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}select{color:white;background-color:#263238;padding:6px 12px;font-size:16px;border:none;border-radius:3px}a{color:#03A9F4}button{background-color:#455A64;color:#FAFAFA;border:none;padding:6px 12px;font-size:16px;text-transform:uppercase;cursor:pointer;border-radius:3px;transition:all .15s ease}button:hover{background-color:#2196F3;box-shadow:0px 5px 10px 0px rgba(0,0,0,0.25)}button:disabled{opacity:0.3;box-shadow:none;cursor:not-allowed}

.container{min-height:100vh;max-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column}.code-editor>div>div{padding-top:20px}

.lds-ellipsis{display:inline-block;position:relative;width:80px;height:10px}.lds-ellipsis div{position:absolute;width:11px;height:11px;border-radius:50%;background:#fff;-webkit-animation-timing-function:cubic-bezier(0, 1, 1, 0);animation-timing-function:cubic-bezier(0, 1, 1, 0)}.lds-ellipsis div:nth-child(1){left:8px;-webkit-animation:lds-ellipsis1 0.6s infinite;animation:lds-ellipsis1 0.6s infinite}.lds-ellipsis div:nth-child(2){left:8px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(3){left:32px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(4){left:56px;-webkit-animation:lds-ellipsis3 0.6s infinite;animation:lds-ellipsis3 0.6s infinite}@-webkit-keyframes lds-ellipsis1{0%{-webkit-transform:scale(0);transform:scale(0)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes lds-ellipsis1{0%{-webkit-transform:scale(0);transform:scale(0)}100%{-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes lds-ellipsis3{0%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(0);transform:scale(0)}}@keyframes lds-ellipsis3{0%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(0);transform:scale(0)}}@-webkit-keyframes lds-ellipsis2{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(24px, 0);transform:translate(24px, 0)}}@keyframes lds-ellipsis2{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(24px, 0);transform:translate(24px, 0)}}

.header{width:100%;background-color:#37474F;box-shadow:0 0 30px 0 rgba(0,0,0,0.2);padding:10px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;z-index:10}.header>*{margin-right:10px}.header>*:last-child{margin-right:0}.header .execute{background-color:#42A5F5}.header .execute:hover{background-color:#2196F3}

.result-viewer{font-family:'Source Code Pro', monospace;z-index:10;width:100%;max-height:50vh;overflow-y:auto;bottom:0;position:absolute;box-shadow:0 0 30px 0 rgba(0,0,0,0.2)}.result-viewer>div{padding:10px;background-color:#263238}.result-viewer .heading{font-size:0.8em;color:#90A4AE;margin-bottom:5px}.result-viewer .stderr{color:#f44336}

